#paper {
  position: relative;
  padding: 6px;
  display: inline-block;
}

.letter {
    width: 100px;
    height: 130px;
    position: relative;
}

.letter:before, .letter:after {
    content: "";
    height: 100%;
    width: 100%;
    position: absolute;
    z-index: -2;
    transition: .2s;
    top: 0;
}

.letter:before {
    left: -5px;
    top: 2px;  
    transform: rotate(-1.5deg);
}

.letter:after {
    right: -3px;
    top: 0px;
    transform: rotate(2.5deg);
}

.letter:hover:before {
  transform: rotate(0deg);
  left: -6px;
  top: -6px; 
}

.letter:hover:after {
  transform: rotate(0deg);
  right: 3px;
  top: -3px;
}